</template>
<style>
</style>
<script>
  import Vue from 'vue'

  export default {
    props: {
      threshold: {  // 可滚动控件滚动偏移多少后才显示回到顶部组件，必须大于 100。默认值为 150
        type: Number,
        required: false,
        default: 150,
        validator (val) {
          return val >= 100
        }
      },
      animationTime: { // 滚动到顶部的动画时间，单位为毫秒，100 到 200 之间。默认值为 150
        type: Number,
        required: false,
        default: 150,
        validator (val) {
          return val >= 100 && val <= 200
        }
      },
      imgUrl: {  // 滚动到顶部控件为图片模式时图片的 url，如果设置了该属性，则会忽略 svg 开头的相关属性。默认值为 null
        type: String,
        required: false,
        default: null
      },
      imgCss: {  // 滚动到顶部控件为图片模式时图片的样式，如果设置了该属性，则会忽略 svg 开头的相关属性。默认值为 null
        type: String,
        required: false,
        default: null
      },
      svgMajorColor: {  // 滚动到顶部控件为 SVG 模式时的主要颜色。默认值为 #bfbfbf
        type: String,
        required: false,
        default: '#bfbfbf'
      },
      svgMinorColor: {  // 滚动到顶部控件为 SVG 模式时的次要颜色。默认值为 #bfbfbf
        type: String,
        required: false,
        default: '#bfbfbf'
      },
      svgType: {  // SVG 模式的样式类型，包括 circle_border_arrow、circle_fill_arrow、rocket_smoke、rocket。默认值为 rocket
        type: String,
        required: false,
        default: 'rocket'
      },
      right: {  // 滚动到顶部控件右边缘离浏览器右边缘的距离，单位为 px，但是开发者只需要写数字就行。默认值为 30
        type: Number,
        required: false,
        default: 30
      },
      bottom: { // 滚动到顶部控件下边缘离浏览器下边缘的距离，单位为 px，但是开发者只需要写数字就行。默认值为 60
        type: Number,
        required: false,
        default: 60
      },
      width: { // 滚动到顶部控件宽度，单位为 px，但是开发者只需要写数字就行。默认值为 48
        type: Number,
        required: false,
        default: 48
      },
      height: { // 滚动到顶部控件高度，单位为 px，但是开发者只需要写数字就行。默认值为 48
        type: Number,
        required: false,
        default: 48
      }
    },
    data () {
      return {
        show: false,
        intervalTime: 20,
        scrollableElement: null,
        scrollToTopTimer: Number.MIN_VALUE,
        addScrollListenerTimeout: Number.MIN_VALUE,
        addScrollListenerTimeoutCount: 0,
        backTopContainerStyle: {
          width: this.width + 'px',
          height: this.height + 'px',
          bottom: this.bottom + 'px',
          right: this.right + 'px',
          position: 'fixed',
          cursor: 'pointer',
          'z-index': 999
        },
        imgStyle: {
          width: this.width + 'px',
          height: this.height + 'px'
        }
      }
    },
    watch: {
      $route (to, from) {
        // 这里先清除滚动监听器「然后延时500毫秒，否则整个项目只添加一个 BgaBackTop 时可能会出现失效」
        this.removeScrollListener()
        setTimeout(this.addScrollListener, 500)
      }
    },
    methods: {
      clearTimer () {
        if (this.scrollToTopTimer !== Number.MIN_VALUE) {
          clearInterval(this.scrollToTopTimer)
          this.scrollToTopTimer = Number.MIN_VALUE
        }
        if (this.addScrollListenerTimeout !== Number.MIN_VALUE) {
          clearTimeout(this.addScrollListenerTimeout)
          this.addScrollListenerTimeout = Number.MIN_VALUE
        }
      },
      removeScrollListener () {
        this.show = false
        this.clearTimer()
        if (this.scrollableElement) {
          this.scrollableElement.removeEventListener('scroll', this.handleScrolling)
          this.scrollableElement = null
        }
      },
      addScrollListener () {
        this.removeScrollListener()
        this.scrollableElement = document.getElementsByClassName('bga-back-top')[0]
        if (this.scrollableElement) {
          this.scrollableElement.addEventListener('scroll', this.handleScrolling)
        } else if (this.addScrollListenerTimeoutCount < 6) {
          // 可能是在可滚动节点上用了 v-if 来判断某个对象（可能生产这个对象时需要从网络加载数据，会要好几秒的时间）是否为空导致获取不到该节点
          // 这里每 1.5 秒获取一次可滚动节点，一共 6 次，如果 6 次后还是获取不到可滚动节点的话就不会再获取了，触发路由发生变化
          this.addScrollListenerTimeout = setTimeout(this.addScrollListener, 1500)
          this.addScrollListenerTimeoutCount++
        }
      },
      handleScrolling (e) {
        if (e.target.scrollTop > this.threshold) {
          this.show = true
        } else {
          this.show = false
        }
      },
      startScrollToTop () {
        if (this.scrollableElement) {
          let scrollTop = this.scrollableElement.scrollTop
          let speed = this.intervalTime / this.animationTime
          this.scrollToTopTimer = setInterval(() => {
            if (this.scrollableElement) {
              scrollTop -= scrollTop * speed
              if (scrollTop <= 1) {
                scrollTop = 0
                this.clearTimer()
              }
              this.scrollableElement.scrollTop = scrollTop
            } else {
              this.clearTimer()
            }
          }, this.intervalTime)
        }
      }
    },
    created () {
      Vue.prototype.$bagBacktopInstance = this
    },
    destroyed () {
      Vue.prototype.$bagBacktopInstance = null
    },
    beforeDestroy () {
      this.removeScrollListener()
    },
    mounted () {
      this.$nextTick(() => {
        this.addScrollListener()
      })
    }
  }
</script>



